.header-menu {
  margin-left: auto;
}

.header__logo {
  padding-right: 33px;
}

.header-menu__logo-wrapper {
  display: none;
}

.header-menu__list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  width: auto;
  padding: 0;
  margin-top: 10px;
}

.header-menu__button {
  display: none;
}

.header-menu__list li:not(:last-child) {
  margin-right: 36px;
}

.header-menu__list a:link {
  color: $alice-blue;

  @include text(16px, 20px);
  text-decoration: none;
}

.header-menu__list a:hover,
.header-menu__list a:focus,
.header-menu__list a:active {
  text-decoration: underline;
  outline: none;
}

@include vp-1365 {
  .header-menu__list li:not(:last-child) {
    margin-right: 24px;
  }

  .header-menu__list {
    margin-top: 0;
  }

  .header-menu__list a:link {
    @include text(14px, 17px);
  }
}

@include vp-767 {
  .header-menu__list {
    display: none;
    width: calc(100% - 35px);
    padding: 75px 30px 200px 15px;
    box-sizing: border-box;
    margin: 0;
    background-color: $white;
  }

  .header__logo-image--mobile {
    display: none;
  }

  .header__logo-image--mobile-visible {
    display: block;
  }

  .header-menu__logo-wrapper {
    position: absolute;
    top: 20px;
    left: 50px;
    display: block;
    z-index: 2;
  }

  .header-menu--closed .header-menu__logo-wrapper {
    display: none;
  }

  .header-menu--nojs .header-menu__list {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }

  .header-menu--opened .header-menu__list {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
  }

  .header-menu--closed .header-menu__button {
    display: block;

    @include sizes(50px, 50px);
    border: none;
    cursor: pointer;
    position: absolute;
    right: 0;
    top: 0;
    padding: 10px;
    background-color: transparent;

    &::before {
      content: "";
      position: absolute;
      top: 18px;
      right: 50%;
      transform: translate(50%, 0);
      display: block;

      @include sizes(24px, 2px);
      background-color: $alice-blue;
      box-shadow: 0 6px 0 0 $alice-blue, 0 12px 0 0 $alice-blue;
    }
  }

  .header-menu--nojs .header-menu__button {
    display: none;
  }

  .header-menu--opened .header-menu__button {
    display: block;

    @include sizes(50px, 50px);
    position: absolute;
    top: 5px;
    right: -5px;
    background-color: transparent;
    border: none;
    cursor: pointer;
    padding: 15px;
    z-index: 2;

    &::before,
    &::after {
      content: "";
      position: absolute;
      display: block;
      top: 25px;
      right: 15px;

      @include sizes(24px, 2px);
      background-color: $tangaroa;
    }

    &::before {
      transform: rotate(45deg);
    }

    &::after {
      transform: rotate(-45deg);
    }
  }

  .header-menu__list li {
    margin-bottom: 8px;
  }

  .header-menu__list a:link {
    display: block;
    padding: 10px;
    width: 100%;
    height: auto;
    box-sizing: border-box;
    text-align: left;
    font-weight: 500;

    @include text(18px, 22px);
    color: $tangaroa;
    position: relative;
  }
}
